<template>
  <div class="grid gap-4">
    <div class="flex bg-blue-lighten text-blue-04 p-4 gap-4 rounded-lg">
      <i class="material-icons">info</i>
      <span
        >Check the boxes next to the carriers you want to buy and leave the
        boxes unchecked for those you do not wish to purchase</span
      >
    </div>
    <Fieldset class="rounded-lg">
      <template #label>
        <span>Carriers</span>
      </template>
      <template #default>
        <Checkbox
          :model-value="isSelectedPartial"
          :indeterminate="isIndeterminate"
          @click.prevent="handleToggleSelectAll"
          class="font-normal ml-5"
          >Select All</Checkbox
        >
        <hr class="border border-grey-lighten" />
        <TreeCheckbox
          v-model="selectedCarriers"
          :items="carriers"
          :by="by"
          :name="name"
        >
        </TreeCheckbox>
      </template>
    </Fieldset>
  </div>
</template>
<script>
import { defineComponent, computed } from 'vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import TreeCheckbox from 'components/TreeCheckbox.vue'

export default defineComponent({
  name: 'DeviceNetworkSetting',
  components: {
    Fieldset,
    Checkbox,
    TreeCheckbox
  },
  props: {
    modelValue: {
      type: Array,
      default: () => []
    },
    carriers: {
      type: Array,
      default: () => []
    },
    by: {
      type: String,
      default: 'network_name'
    },
    name: {
      type: String,
      default: 'network_name'
    }
  },
  emits: ['update:carriers', 'update:modelValue'],
  setup(props, { emit }) {
    const selectedCarriers = computed({
      get: () => {
        return props.modelValue
          .filter((carrier) => carrier.network_name)
          .map((carrier) => carrier.network_name)
      },
      set: (selectedNetworkNames) => {
        const _selectedCarriers = props.carriers
          .filter((carrier) =>
            carrier?.network_name
              ? selectedNetworkNames.includes(carrier.network_name)
              : false
          )
          .map((carrier) => ({ ...carrier }))
        emit('update:modelValue', _selectedCarriers)
      }
    })
    const carriers = computed(() => props.carriers)
    const isSelectedAll = computed(
      () => props.carriers.length === selectedCarriers.value.length
    )
    const isSelectedPartial = computed(() => selectedCarriers.value.length > 0)
    const isIndeterminate = computed(
      () => isSelectedPartial.value && !isSelectedAll.value
    )

    const selectAllCarriers = () => {
      selectedCarriers.value = props.carriers.map(
        (carrier) => carrier.network_name
      )
    }
    const unselectAllCarriers = () => {
      selectedCarriers.value = []
    }
    const handleToggleSelectAll = () => {
      if (isIndeterminate.value || selectedCarriers.value.length === 0) {
        selectAllCarriers()
        return
      }
      unselectAllCarriers()
    }
    return {
      selectedCarriers,
      carriers,
      isSelectedAll,
      isSelectedPartial,
      isIndeterminate,
      handleToggleSelectAll
    }
  }
})
</script>
