<template>
  <Modal v-model="stateCategoryBuybackType.modal" title="Category Settings" hide-footer size="md">
    <template #default>
      <DeviceCategorySetting :price-upload-process="priceUploadProcess" />
    </template>
  </Modal>
  <Modal title="Cell Carriers Settings" v-model="isShowDeviceNetworkSetting">
    <template #default>
      <div v-loading="loading.carriers">
        <DeviceNetworkSetting
          :model-value="carriersModel"
          @update:model-value="handleUpdateCarrier"
          :carriers="carriers"
        />
      </div>
    </template>
    <template #footer>
      <Tooltip
        :disabled="carriersModel.length > 0"
        placement="top"
        content="At least one option must be selected"
      >
        <template #default>
          <div class="inline-block">
            <Button
              @click="handleUpdate"
              :loading="loading.carriers"
              :disabled="carriersModel.length < 1"
              color="teal"
            >
              Save Changes
            </Button>
          </div>
        </template>
      </Tooltip>
    </template>
  </Modal>
  <div class="flex items-stretch mb-2">
    <Tab ref="tabElement" class="categories-tab w-full" :items="categoryList">
      <template #title="scope">
        <div
          v-if="scope.item?.image"
          class="svg-load h-8 w-8 mr-4 bg-teal-04"
          :style="`-webkit-mask-image: url('${scope.item?.image}')`"
        ></div>
        {{ scope.item.name }}
        <Tooltip placement="top">
          <template #content
            ><p
              v-if="scope.item?.set_pricing_status?.tooltip"
              class="w-96"
              v-html="scope.item?.set_pricing_status?.tooltip"
            >
          </p>
          <p v-else>Upload ISBN</p>
          </template>
          <template #default>
            <div>
              <Button
                v-if="scope.item.id === 'books'"
                :disabled="!scope.item?.set_pricing_status?.is_available"
                view="smooth"
                dense
                color="teal"
                class="hover:bg-teal-01 ml-4"
                @click.prevent="handleOpenISBNModal"
              >
                <i class="material-icons">upload</i>
              </Button>
            </div>
          </template>
        </Tooltip>
      </template>
    </Tab>
    <!-- <Popover v-if="isLimit" :visible="isShowCategoryDropdown" :width="180"> -->
    <Popover v-if="isLimit" :visible="isShowCategoryDropdown" :width="180">
      <template #default>
        <div style="max-height: 500px; overflow-y: auto; overflow-x: hidden;">
          <div
            v-for="(item, itemIndex) of moreCategoryList"
            @click="handleSelectSubCategoryDropdownItem(item.to)"
            :key="itemIndex"
            class="flex items-center px-3 py-4 cursor-pointer hover:bg-grey-lighten text-left"
            style="word-break: break-word;"
          >
            <div
              class="svg-load h-10 w-10 mr-4 bg-teal-04"
              :style="`-webkit-mask-image: url('${item?.image}')`"
            ></div>
            <span class="w-full">{{ item.name }}</span>
          </div>
        </div>
      </template>
      <template #reference>
        <div class="flex">
          <div
            v-if="!priceUploadProcess"
            v-outside="handleClickOutsideDropdown"
            @click="isShowCategoryDropdown = !isShowCategoryDropdown"
            class="flex items-center justify-center w-full max-w-max px-4 bg-grey-01 overflow-hidden cursor-pointer"
          >
            <span class="whitespace-nowrap mr-2">{{
              isActiveItemOnMoreCategory && currentActiveCategory
                ? currentActiveCategory?.name
                : 'More Categories'
            }}</span>
            <i class="material-icons">arrow_drop_down</i>
          </div>
          <div
            v-else
            class="flex items-center justify-center w-full max-w-max px-4 bg-grey-01 overflow-hidden cursor-pointer">
            <span class="whitespace-nowrap mr-2">More Categories</span>
            <i class="material-icons">arrow_drop_down</i>
          </div>
          <div
            @click="handleOpenModalBuybackType"
            class="select-none transition duration-200 cursor-pointer bg-teal-01 hover:bg-teal-04 hover:text-teal-01 text-teal-04 px-4 flex items-center justify-center"
          >
            <i class="material-icons">settings</i>
          </div>
        </div>
      </template>
    </Popover>
  </div>
  <router-view />
</template>
<script>
import { defineComponent, computed, ref, provide } from 'vue'
import { useBuybackDevice, globalStateDeviceCategoryBuybackType as stateCategoryBuybackType } from 'composable/setting'
import { processing as processingPriceImport } from 'composable/pricing'
import { useRouter, useRoute } from 'vue-router'
import debounce from 'utils/debounce'
import DeviceTable from 'components/BuybackDevice/DeviceTable.vue'
import Pagination from 'components/Pagination.vue'
import DeviceCategorySetting from 'components/BuybackDevice/DeviceCategorySetting.vue'
import Card from 'bbUi/components/card/card.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import ExpandablePanel from 'bbUi/components/expandable-panel/expandable-panel.vue'
import Breadcrumb from 'bbUi/components/breadcrumb/breadcrumb.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import SVGLoader from 'components/SVGLoader.vue'
import DeviceNetworkSetting from 'components/BuybackDevice/DeviceNetworkSetting.vue'

export default defineComponent({
  components: {
    Card,
    Checkbox,
    ExpandablePanel,
    Breadcrumb,
    Input,
    Button,
    Pagination,
    DeviceCategorySetting,
    DeviceTable,
    Tab,
    Modal,
    SVGLoader,
    DeviceNetworkSetting
  },
  async setup(props, { emit }) {
    const { categoriesTab, actionCategories, actionCarriers, actionUpdateCarriers, carriers, loading } = useBuybackDevice()
    const router = useRouter()
    const route = useRoute()
    
    const selectedAllCarries = ref(false)
    const indeterminateCarriers = ref(false)
    const carriersModel = ref([])
    const tabElement = ref(null)
    const isShowDeviceNetworkSetting = ref(false)

    const limitItemNumber = computed(() => {
      let sumWidth = 0
      let limitIndex = 0
      if(!tabElement.value) return 0
      for(let [itemIndex, item] of Object.entries(Array.from(tabElement.value.$el.children))){
        const itemStyle = window.getComputedStyle(item)
        if(sumWidth > tabElement.value.$el.offsetWidth) break
        sumWidth += parseFloat(itemStyle.width) + parseFloat(itemStyle.marginRight) + parseFloat(itemStyle.marginLeft)
        limitIndex = itemIndex
      }
      return limitIndex
    })
    
    const url = computed(() => window.location.origin)
    const isLimit = computed(
      () => limitItemNumber.value < categoriesTab.value.length - 1
    )
    const categoryList = computed(() => limitCategories(0, isLimit.value ? limitItemNumber.value : 0))
    const moreCategoryList = computed(() =>
      limitCategories(limitItemNumber.value, categoriesTab.value.length)
    )
    const isActiveItemOnMoreCategory = computed(() =>
      moreCategoryList.value
        .map((category) => category.to.params.category)
        .includes(route.params.category)
    )
    const currentActiveCategory = computed(
      () =>
        categoriesTab.value.find(
          (tab) => tab.to.params.category === route.params.category
        ) || null
    )
    const isShowCategoryDropdown = ref(false)
    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Catalog'
      }
    ]

    const limitCategories = (fromIndex, toIndex) => {
      if (toIndex === 0) return categoriesTab.value
      if (fromIndex < categoriesTab.value.length)
        return categoriesTab.value.slice(fromIndex, toIndex)
      return categoriesTab.value
    }
    const handleSelectSubCategoryDropdownItem = (to) => {
      if (!!to) router.push(to)
      isShowCategoryDropdown.value = false
    }
    const handleClickOutsideDropdown = () =>
      (isShowCategoryDropdown.value = false)
    const handleOpenModalBuybackType = () => {
      stateCategoryBuybackType.modal = true
      handleClickOutsideDropdown()
    }

    const isDisabledAll = computed(() => {
      const count = [...carriersModel.value].filter(item => item.checked).length
      return count >= carriersModel.value.length || count <= 1
    })
    const isDisabledCheckBoxCarriers = computed(() => {
      const count = [...carriersModel.value].filter(item => item.checked).length
      return count <= 1
    })
    const selectedNetworkNames = computed(() => carriersModel.value.filter((carrier) => carrier.network_name).map((carrier) => carrier.network_name))
    const priceUploadProcess = computed(() =>
      processingPriceImport.value.uploadProgress > 0 &&
      processingPriceImport.value.uploadProgress < 100
    )

    const handleGetCarriers = async () => {
      await actionCarriers()
      carriersModel.value = [...carriers.value].filter((carrier) => carrier.is_enable == 1).map((item) => ({
        ...item,
        checked: item.is_enable ? true : false
      }))
      const countActive = [...carriersModel.value].filter(item => item.checked).length
      if (countActive === carriers.value.length) {
        selectedAllCarries.value = true
        indeterminateCarriers.value = false
      } else {
        selectedAllCarries.value = false
        indeterminateCarriers.value = true
      }
    }
    const handleChangeAll = (val) => {
      const count = [...carriersModel.value].filter(item => item.checked).length
      carriersModel.value = carriersModel.value.map((item, _, self) => {
        if (count > 0 && count < self.length) {
          selectedAllCarries.value = true
        }
        return ({
          ...item,
          checked: count > 0 && count < self.length ? true : val
        })
      })
      indeterminateCarriers.value = false
    }
    const handleCheckedCarriersChange = () => {
      const count = [...carriersModel.value].filter(item => item.checked).length
      selectedAllCarries.value = count === carriersModel.value.length
      indeterminateCarriers.value = count > 0 && count < carriersModel.value.length
    }
    const handleUpdate = debounce(() => {
      const data = carriers.value.map((carrier) => {
        if (selectedNetworkNames.value.includes(carrier.network_name))
          return { network_name: carrier.network_name, is_enable: 1 }
        return { network_name: carrier.network_name, is_enable: 0 }
      })
      actionUpdateCarriers(data).then(() => {
        isShowDeviceNetworkSetting.value = false
      })
    }, 300)
    const handleOpenISBNModal = () => {
      router.push({
        name: 'settingBuybackDeviceTab',
        params: {
          category: 'books'
        },
        query: {
          isbn: 'update'
        }
      })
    }
    const handleUpdateCarrier = (selectedCarriers=[]) => {
      carriersModel.value = selectedCarriers
    }
    const openNetworkSetting = async () => {
      isShowDeviceNetworkSetting.value = true
      await handleGetCarriers()
    }
    
    provide('openNetworkSetting', openNetworkSetting)

    await actionCategories()
    await handleGetCarriers()

    return {
      tabElement,
      isShowDeviceNetworkSetting,
      url,
      limitItemNumber,
      categoriesTab,
      breadcrumb,
      isLimit,
      isShowCategoryDropdown,
      categoryList,
      moreCategoryList,
      isActiveItemOnMoreCategory,
      currentActiveCategory,
      stateCategoryBuybackType,
      limitCategories,
      handleSelectSubCategoryDropdownItem,
      handleClickOutsideDropdown,
      handleOpenModalBuybackType,
      selectedAllCarries,
      indeterminateCarriers,
      handleChangeAll,
      handleCheckedCarriersChange,
      handleUpdate,
      handleOpenISBNModal,
      handleUpdateCarrier,
      carriers,
      carriersModel,
      loading,
      isDisabledAll,
      isDisabledCheckBoxCarriers,
      priceUploadProcess
    }
  }
})
</script>

<style lang="scss" scoped>
.categories-tab {
  margin: 0 !important;
}
.svg-load {
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}
</style>
