<template>
	<div class="grid grid-cols-1 gap-6">
		<div>
			<Input
				v-model="stateCategoryBuybackType.filters.search"
				placeholder="Search Category"
				icon
				block>
				<template #icon>
					<i class="text-grey-04 material-icons">search</i>
				</template>
			</Input>
		</div>
		<div>
			<Table
				v-loading="
					stateCategoryBuybackType.loading.all ||
					stateCategoryBuybackType.loading.update.status.get('ALL')
				"
				:data="data"
				:max-height="680"
				:row-class-name="priceUploadProcess && 'global-disabled-row-table'">
				<TableColumn width="100">
					<template #header>
						<Checkbox
							:modelValue="stateCategoryBuybackType.data?.is_enable"
							@input="(value) => handleUpdateCategoryStatus(null, value)"
							:value="1"
							:value-off="0"
							:disabled="priceUploadProcess"
							type="toggle" />
					</template>
					<template #default="{ row }">
						<Checkbox
							:modelValue="row.is_enable"
							@input="(value) => handleUpdateCategoryStatus(row, value)"
							:value="1"
							:value-off="0"
							:loading="
								stateCategoryBuybackType.loading.update.status.get(row.name)
							"
							:disabled="priceUploadProcess"
							type="toggle" />
					</template>
				</TableColumn>
				<TableColumn label="Category" prop="name"></TableColumn>
				<TableColumn label="">
					<template #default="{ row }">
						<div class="flex items-center justify-center">
							<div class="mr-4">
								<Checkbox
									@input="
										(value) =>
											handleUpdateCategoryBuybackType(row, value, 'instore')
									"
									:modelValue="row.instore === 2 ? 0 : row.instore"
									:indeterminate="row.instore === 2"
									:value="1"
									:value-off="0"
									:disabled="row.is_enable === 0 || priceUploadProcess">
									<span class="font-normal">In-Store</span>
								</Checkbox>
							</div>
							<div class="mr-6">
								<Checkbox
									@input="
										(value) =>
											handleUpdateCategoryBuybackType(row, value, 'mailin')
									"
									:modelValue="row.mailin === 2 ? 0 : row.mailin"
									:indeterminate="row.mailin === 2"
									:value="1"
									:value-off="0"
									:disabled="row.is_enable === 0 || priceUploadProcess">
									<span class="font-normal">Mail-In</span>
								</Checkbox>
							</div>
							<div class="relative">
								<transition name="fade">
									<Loading
										v-show="
											stateCategoryBuybackType.loading.update.buyback_type.get(
												row.name
											)
										" />
								</transition>
							</div>
						</div>
					</template>
				</TableColumn>
			</Table>
			<Pagination
				v-if="stateCategoryBuybackType.data"
				v-model:page="stateCategoryBuybackType.filters.page"
				v-model:limit="stateCategoryBuybackType.filters.limit"
				:data="stateCategoryBuybackType.data"
				class="mt-6" />
		</div>
	</div>
</template>

<script>
import { computed, defineComponent, onMounted, watch } from 'vue'
import { useRoute } from 'vue-router'
import {
	globalStateDeviceCategoryBuybackType as stateCategoryBuybackType,
	useDeviceCategoryBuybackType,
	useBuybackDevice
} from 'composable/setting'

import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Loading from 'bbUi/components/loading/loading.vue'

import Pagination from 'components/Pagination.vue'

import debounce from 'utils/debounce'
import { ElMessage } from 'element-plus'

export default defineComponent({
	components: {
		Input,
		Checkbox,
		Pagination,
		Loading
	},
	props: {
		priceUploadProcess: {
			type: Boolean,
			default: false
		}
	},
	emits: ['update-all'],
	setup() {
		const { action, actionUpdateBuybackType, actionUpdateStatus } =
			useDeviceCategoryBuybackType()
		const { actionAll } = useBuybackDevice()
		const route = useRoute()
		const data = computed(() =>
			stateCategoryBuybackType.data ? stateCategoryBuybackType.data.data : []
		)

		onMounted(async () => {
			await action()
		})

		watch(
			() => ({
				search: stateCategoryBuybackType.filters.search
			}),
			() => {
				stateCategoryBuybackType.filters.page = 1
			},
			{
				deep: true
			}
		)

		watch(
			() => stateCategoryBuybackType.filters,
			debounce(async () => await action(), 500),
			{
				deep: true
			}
		)

		const handleUpdateCategoryBuybackType = async (
			item,
			value,
			type = null
		) => {
			const validTypes = ['mailin', 'instore']
			const isInvalidType = !validTypes.includes(type)
			if (!type || isInvalidType) return

			const currentState = {...item}

			item[type] = value
			const { name: category } = item
			const payload = {
				category,
				value,
				type
			}
			await actionUpdateBuybackType(payload, async (err) => {
				if(err) Object.assign(item, currentState)
				else {
					await Promise.all([
						action(false, (response) => {
							stateCategoryBuybackType.is_enable = response.is_enable
						}),
						actionAll(
							route.params.category,
							route.params.brand == 'all' ? null : route.params.brand
						)
					])
					if (item.mailin === 0 && item.instore === 0) item.is_enable = 0
				}
			})

		}
		const handleUpdateCategoryStatus = async (item, value) => {
			const payload = {
				is_mac: item !== null ? item.is_computer : 0,
				is_enable: value,
				category: item !== null ? item.name : 'ALL'
			}

			if(item) item.is_enable = value
			else stateCategoryBuybackType.data.is_enable = value

			if (['Golf Clubs'].includes(item?.name)) {
				ElMessage({
					message: 'Feel free to navigate away or close this window',
					type: 'info'
				})
			}

			await actionUpdateStatus(payload, async (err) => {
				if(err){
					setTimeout(() => {
						if(item) item.is_enable = item.is_enable == 1 ? 0 : 1
						else stateCategoryBuybackType.data.is_enable = stateCategoryBuybackType.data.is_enable == 1 ? 0 : 1
					}, 1000)
				} else {
					if(item){
						item.instore = value
						item.mailin = value
					}
					await action(payload.category === 'ALL' ? true : false, (response) => {
						stateCategoryBuybackType.data.is_enable = response.is_enable
					})
					await actionAll(
						route.params.category,
						route.params.brand == 'all' ? null : route.params.brand
					)
					ElMessage({
						type: 'success',
						message: 'Item status updated'
					})
				}
			})

			
		}

		return {
			stateCategoryBuybackType,
			data,
			handleUpdateCategoryBuybackType,
			handleUpdateCategoryStatus
		}
	}
})
</script>

